<template>
  <div class="mangaList">     
    <b-row id="buttonRow">       
        <router-link                 
        tag="b-button"
        class="buttons"
        :to="{name:'Home'}"                        
        >
        Voltar
        </router-link>   
        <b-button class="buttons" @click="showAddForm()">Adicionar</b-button>
    </b-row>
    <b-row id='formRow'>
        <MangaForm @added="addFeedBack" v-show="addFormShow"/>
    </b-row>
    <b-alert fade class="successAlert"
        :show="dismissCountDown"
        dismissible
        variant="success"                    
        @dismissed="dismissCountDown=0"
        @dismiss-count-down="countDownChanged"
        >                   
        {{alertMessage}}                    
    </b-alert>    
    <b-table id="mangaTable" responsive striped hover :items="getAllMangas" :fields="fields">
        <template v-slot:cell(titulo)="data">
            <router-link
            :to="{name:'Manga', params:{ id: data.item.id}}"
            >
            {{data.item.titulo}}</router-link>
        </template>
        <template v-slot:cell(criado)="data">
            {{ data.item.criado | formatDate()}}
        </template>
        <template v-slot:cell(preco)="data">
            {{ data.item.preco | formatPrice('R$') }}
        </template>
        <template v-slot:cell(favorito)="data">
            {{ data.item.favorito ? "Sim" : 'Não' }}
        </template>
        <template v-slot:cell(apagar)="data">
            <b-button size="sm" variant="light">
                <b-icon
                @click="submitDelete(data.item.id)"
                icon="trash" variant="dark"
                font-scale="2"
                >
                </b-icon></b-button>  
        </template>       
       
    </b-table>
  </div>
  
</template>

<script>
import {mapGetters, mapActions} from 'vuex'
import MangaForm from './MangaForm'
export default {
    name: 'MangaList',
    
    components: {
        MangaForm
    },

    

    data: function(){
        return {
            fields: [
                'titulo',
                { key: 'criado',label: 'Criado'},
                'subtitulo',
                'autor',
                'editora',
                { key: 'edicao', label: 'Edição'},
                { key: 'preco', label: 'Preço' },
                { key: 'favorito', label: 'Favorito'},
                { key: 'apagar', label: ''},
                
            ],

            addFormShow: false,

            dismissSecs: 5,
            dismissCountDown: 0,  
            alertMessage: ''
        }
    },

    methods:{
        ...mapActions(['deleteManga']),

        showAddForm(){      
            this.addFormShow = this.addFormShow ? false : true
        },

        submitDelete(id){
            this.deleteManga(id);
            this.alertMessage = 'Removido com Sucesso!'
            this.showAlert();           

        },

        addFeedBack(){
            this.alertMessage = 'Adicionado com sucesso!'            
            this.showAlert();
        },

        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown
        },

        showAlert() {
            this.dismissCountDown = this.dismissSecs
        }
    },    

    computed: {
        ...mapGetters(['getAllMangas'])
    }

}
</script>

<style>
    #buttonRow{
        justify-content: space-between;
        margin: 20px 0px;
    }

    .buttons{
        margin: 0px 15px;
    }
    a{
        color: #212529
    }
</style>